import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage';
import { Platform } from '@ionic/angular';
import {environment} from '../../../environments/environment';
import { BehaviorSubject } from 'rxjs';


@Injectable({
    providedIn: 'root'
})
export class NotificationsService {
    
    notificationsState = new BehaviorSubject(false);
    
    constructor(public http: HttpClient, public storage: Storage) { }
    
    public monitorNotifications() {
        return this.notificationsState.asObservable();
    }      
    
    public notificationsUpdated(){
        this.notificationsState.next(true);
    }  
        
   public getNotifications(pagination){
        return new Promise((resolve, reject) => {
            this.storage.get("mb_token").then((token) => {            
                if (token){            
                    this.http.get(environment.apiUrl + "/notifications?token=" + token + "&page=" + pagination.page + "&limit=" + pagination.limit).subscribe((res:any) => {
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }  
    
    public getNotificationCount(){
        return new Promise((resolve, reject) => {
            this.storage.get("mb_token").then((token) => {            
                if (token){            
                    this.http.get(environment.apiUrl + "/notificationcount?token=" + token).subscribe((res:any) => {
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }     
    
    
    public markAllAsSeen(){
        return new Promise((resolve, reject) => {
            this.storage.get("mb_token").then((token) => {            
                if (token){            
                    this.http.get(environment.apiUrl + "/notificationsseen?token=" + token).subscribe((res:any) => {
                        this.notificationsState.next(true);
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }    
    
    
    public updateNotification(notification){
        return new Promise((resolve, reject) => {
            this.storage.get("mb_token").then((token) => {            
                if (token){            
                    this.http.put(environment.apiUrl + "/notifications/" + notification.id + "?token=" + token, notification).subscribe((res:any) => {
                        this.notificationsState.next(true);
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }   
        
}
