import {fetch as fetchPolyfill} from 'whatwg-fetch';
window.fetch = fetchPolyfill;

import { Component, NgZone } from '@angular/core';

import { Platform, ModalController } from '@ionic/angular';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { StatusBar } from '@ionic-native/status-bar/ngx';

import { Router, RouterEvent, NavigationEnd } from '@angular/router';
import { AuthenticationService } from './services/authentication/authentication.service';
import { MusicianService } from './services/musician/musician.service';
import {NotificationsService} from './services/notifications/notifications.service';
import { OneSignal } from '@ionic-native/onesignal/ngx';

import { Storage } from '@ionic/storage';

declare const cordova: any;

import {NotificationsPage} from './pages/notifications/notifications.page';

@Component({
    selector: 'app-root',
    templateUrl: 'app.component.html',
    styleUrls: ['app.component.scss']
})
export class AppComponent {
    public user:any;
    
    constructor(public platform: Platform, private splashScreen: SplashScreen, private statusBar: StatusBar, 
        private authenticationService: AuthenticationService, private router: Router, public storage: Storage, 
        private oneSignal: OneSignal, private ngZone: NgZone, private notificationsService: NotificationsService, public modalController: ModalController,
        private musicianService: MusicianService){

        this.initializeApp();
        
        this.authenticationService.getUserData().subscribe(value => {  
            
            this.user = {};
            
            this.ngZone.run(() => {  
                
                this.user = value;
                this.savePushId();
            
            });
            
        });         
        
        
        
    }


    savePushId(){
        this.oneSignal.getIds().then((data) => {
            if (data.userId){
                //save
                this.authenticationService.savePushId(data.userId).then(() => {});
            }
        }).catch((e) => {
            //console.log(e);
        });        
    } 
    
    public async openNotifications(){
        const modal = await this.modalController.create({
          component: NotificationsPage,
          componentProps: {}
        });

        await modal.present();         
    }      

    initializeApp() {
        this.platform.ready().then(() => {
        this.statusBar.styleDefault();
        if (this.platform.is("android")) {
            this.statusBar.backgroundColorByHexString("#000000");
            this.statusBar.styleLightContent();
        }
        else{
            this.statusBar.styleLightContent();
        }

        
        
        
        
        if (this.platform.is('cordova')){

            this.oneSignal.startInit("170f44ed-1f86-4556-b650-f1b674f0183c", "412694809389");
            
            this.oneSignal.handleNotificationReceived().subscribe((data) => {
                data.payload.additionalData["title"] = data.payload.title;
                let pushData = data.payload.additionalData;    
                
                this.ngZone.run(() => {                      
                    this.notificationsService.notificationsUpdated();        
                        
                });  
                  
            });                



            this.oneSignal.handleNotificationOpened().subscribe((data) => {
                if (!data.notification.isAppInFocus){
                    let pushData = data.notification.payload.additionalData;
                    
                    setTimeout(() => {
                        this.ngZone.run(() => {  
                            this.openNotifications();
                        });
                    
                    },1000);                    
                    
                    

                }
            });

            this.oneSignal.endInit();    
            
            
           cordova.plugins.stripe.setPublishableKey('pk_test_51Gsfe9EhuErkODNStyYjXGWYpjgOKjHYMQE2Oq5d6zYCX0DpIGLrUsAavwwz44fNyU8xwednNWE65GEbA9Wdbxrw00IJVJgyGR');

        }
   
                
        
        this.authenticationService.retreiveUserData().then(() => {}).catch(() => {});        
   
        
        this.authenticationService.authState.subscribe(state => {
            if (state) {
                this.router.navigate(['']);
            } else {
                this.router.navigate(['start']);
            }
        });     
        

        setTimeout(() => {this.splashScreen.hide(); },500);
        
        })
    }
}
