import { Component, OnInit } from '@angular/core';
import { ModalController, NavParams, AlertController,NavController } from '@ionic/angular';
import { AuthenticationService } from '../../services/authentication/authentication.service';
import { NotificationsService } from '../../services/notifications/notifications.service';

import * as moment from 'moment';

@Component({
    selector: 'app-notifications',
    templateUrl: './notifications.page.html',
    styleUrls: ['./notifications.page.scss'],
})
export class NotificationsPage implements OnInit {
    
    
    public notifications:any;
    public user:any;
    public loading:boolean;
    public page:number;
    
    constructor(public modalController: ModalController,private navParams: NavParams, private authenticationService:AuthenticationService,
        private notificationsService: NotificationsService, private alertController: AlertController, private navController: NavController) { 
    
        this.notifications = {data:[], count:0};
        
        this.page = 1;
        this.loading = true;
        
        this.getNotifications();
        
        this.markAllAsSeen(); 
    }

    ngOnInit() {
    }

    
        
    public getNotifications(){
        
        this.notificationsService.getNotifications({limit:10,page:this.page}).then((data) => {
            this.notifications = data;
            this.loading = false;
        }).catch(() => {
            this.loading = false;
        })
    }
    
    public getMoreNotifications(infiniteScroll){

        this.page = this.page + 1;
        
        this.notificationsService.getNotifications({limit:10,page:this.page}).then((data) => {
            
            for (let item of data["data"]){
                this.notifications.data.push(item);
            }  
                    
            infiniteScroll.complete();
        })
        .catch((e) => {
            infiniteScroll.complete();
        })
    }
    
    public markAllAsSeen(){
        this.notificationsService.markAllAsSeen().then(() => {
            
        });
    }
    
    
    public openNotification(notification){
        //update as seen in api
        //go to item in app
        
        if (!notification.seen){
            notification.seen = true;
            this.notificationsService.updateNotification({seen: true, id: notification.id}).then(() => {
                
            });        
        }
        
        
        
        
        if (notification.notification_type === "gig"){ 
            //view job 
            this.navController.navigateForward('/gigs/' + notification.source_id);
            this.dismiss();
        }
        else if (notification.notification_type === "profile"){

            this.navController.navigateForward('/profiles/' + notification.source_id);
            this.dismiss();                    
        }   
        else{
            this.viewNotification(notification);  
        }
        
              
    }
    
    
    async viewNotification(notification){
   
        
        const alert = await this.alertController.create({
          header: notification.heading,
          subHeader: "Created " + this.formatDate(notification.created_at),
          message: notification.content,
          buttons: ['Dismiss']
        });

        await alert.present();               
                 
    }

    
    public formatDate(date){
        return moment(date).fromNow();
    }   
    
    public dismiss(){
        this.modalController.dismiss();
    }        
    
}
