import { Component, OnInit } from '@angular/core';
import { ModalController, NavParams } from '@ionic/angular';
import {CityService} from '../../services/city/city.service';
import { AuthenticationService } from '../../services/authentication/authentication.service';

@Component({
    selector: 'app-filters',
    templateUrl: './filters.page.html',
    styleUrls: ['./filters.page.scss'],
})
export class FiltersPage implements OnInit {
    
    
    public filters:any;
    public instruments:Array<any>;
    public user:any;
    
    constructor(public modalController: ModalController,private navParams: NavParams, private cityService:CityService, private authenticationService:AuthenticationService) { 
    
        let filters = this.navParams.get('filters');
        this.filters = filters ? filters : {};  
        
        this.instruments = ["Vocals","Guitar", "Bass", "Piano", "Banjo", "Cello", "Clarinet", "Drums", "Flute", "Trumpet", "Harmonica", "Organ", "Recorder", "Saxophone", "Trombone", "Ukelele", "Tuba", "Viola", "Violin", "Xylophone"].sort();
          
        
    
    }

    ngOnInit() {
    }
    
    
    public update(){
        this.modalController.dismiss({filters:this.filters});
        
    } 
    
    public dismiss(){
        this.modalController.dismiss();
    }  
 

}
