import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage';
import { Platform } from '@ionic/angular';
import {environment} from '../../../environments/environment';
import { BehaviorSubject } from 'rxjs';


@Injectable({
    providedIn: 'root'
})
export class MusicianService {


    musiciansState = new BehaviorSubject(false);

    constructor(public http: HttpClient, public storage: Storage) { }
    
    public monitorMusicians() {
        return this.musiciansState.asObservable();
    }   
    
    public updateMusicians(){
        this.musiciansState.next(true);
    }   
        
    public getMusicians(search, filters){
        return new Promise((resolve, reject) => {
            this.storage.get("mb_token").then((token) => {            
                if (token){            
                    this.http.get(environment.apiUrl + "/profiles?token=" + token + "&search=" + search + "&instruments=" + filters.instruments + "&orderby=" + filters.orderby + "&order=" + filters.order).subscribe((res) => {
                        
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }      
    

    
    public getMusician(musicianId){
        return new Promise((resolve, reject) => {
            this.storage.get("mb_token").then((token) => {            
                if (token){            
                    this.http.get(environment.apiUrl + "/musicians/" + musicianId + "?token=" + token).subscribe((res) => {
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }  
    
    
    public createMusician(musician){
        return new Promise((resolve, reject) => {
            this.storage.get("mb_token").then((token) => {            
                if (token){            
                    this.http.post(environment.apiUrl + "/musicians?token=" + token, musician).subscribe((res) => {
                        this.musiciansState.next(true);
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }     
      
    public updateMusician(musician){
        return new Promise((resolve, reject) => {
            this.storage.get("mb_token").then((token) => {            
                if (token){            
                    this.http.put(environment.apiUrl + "/musicians/" + musician.id + "?token=" + token, musician).subscribe((res) => {
                        this.musiciansState.next(true);
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }  
    
    
    public deleteMusician(musicianId){
        return new Promise((resolve, reject) => {
            this.storage.get("mb_token").then((token) => {            
                if (token){            
                    this.http.delete(environment.apiUrl + "/musicians/" + musicianId + "?token=" + token).subscribe((res) => {
                        this.musiciansState.next(true);
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }    
    
    
    public getCategories(parentId){
        return new Promise((resolve, reject) => {
            this.storage.get("mb_token").then((token) => {            
                if (token){            
                    this.http.get(environment.apiUrl + "/categories?parent_id=" + parentId + "&token=" + token).subscribe((res) => {
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }     
    
    
    public getCategory(categoryId){
        return new Promise((resolve, reject) => {
            this.storage.get("mb_token").then((token) => {            
                if (token){            
                    this.http.get(environment.apiUrl + "/categories/" + categoryId + "?token=" + token).subscribe((res) => {
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }       
    
    
    
    
    public createApplication(application){
        return new Promise((resolve, reject) => {
            this.storage.get("mb_token").then((token) => {            
                if (token){            
                    this.http.post(environment.apiUrl + "/applications?token=" + token, application).subscribe((res) => {
                        this.musiciansState.next(true);
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }    
    
    public acceptApplication(applicationId){
        return new Promise((resolve, reject) => {
            this.storage.get("mb_token").then((token) => {            
                if (token){            
                    this.http.put(environment.apiUrl + "/applications/" + applicationId + "?token=" + token, {status:"accepted"}).subscribe((res) => {
                        this.musiciansState.next(true);
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }  
    
    
    public declineApplication(applicationId){
        return new Promise((resolve, reject) => {
            this.storage.get("mb_token").then((token) => {            
                if (token){            
                    this.http.put(environment.apiUrl + "/applications/" + applicationId + "?token=" + token, {status:"declined"}).subscribe((res) => {
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }        
     
    public completeMusician(data){
        return new Promise((resolve, reject) => {
            this.storage.get("mb_token").then((token) => {            
                if (token){            
                    this.http.post(environment.apiUrl + "/completemusician?token=" + token, data).subscribe((res) => {
                        this.musiciansState.next(true);
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }             
}
